import React from "react";

import { makeStyles } from "@material-ui/core";
import { useCollectionData } from "react-firebase-hooks/firestore";
const useStyles = makeStyles({});

function TestArea({ firebase }) {
  const dummy = React.useRef();
  const messagesRef = firebase.firestore.collection("messages");
  const query = messagesRef.orderBy("createdAt", "asc").limitToLast(25);

  const [messages] = useCollectionData(query, { idField: "id" });
  const [formValue, setFormValue] = React.useState("");

  const scrollToBottom = () => {
    dummy.current.scrollIntoView({ behavior: "smooth" });
  };

  React.useEffect(scrollToBottom, [messages]);

  const sendMessage = async (e) => {
    e.preventDefault();

    const { displayName, uid, photoURL } = firebase.auth.currentUser;

    await messagesRef.add({
      user: displayName,
      body: formValue,
      createdAt: new Date(),
      uid: uid,
      photoURL: photoURL,
    });

    setFormValue("");
    dummy.current.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <div className="chat-bg w-full sm:w-2/3 p-2 rounded">
      <div className="overflow-y-auto h-screen-90">
        {messages &&
          messages.map((msg) => (
            <ChatMessage key={msg.id} message={msg} auth={firebase.auth} />
          ))}
        <span ref={dummy}></span>
      </div>

      <form onSubmit={sendMessage} className="pt-3 w-full inline-flex">
        <input
          className="rounded-3xl px-3 w-full py-1 outline-none focus:shadow"
          value={formValue}
          onChange={(e) => setFormValue(e.target.value)}
          placeholder="Say something"
        />
        <button
          className={`material-icons p-2 mx-2 bg-white rounded-full transition-all duration-75 ease-in-out text-xl ${
            !formValue || "text-pink-700 hover:text-pink-900"
          }`}
          type="submit"
          disabled={!formValue}
        >
          send
        </button>
      </form>
    </div>
  );
}
export default TestArea;

function ChatMessage(props) {
  const { user, body, uid, photoURL, createdAt } = props.message;

  const messageClass =
    uid === props.auth.currentUser.uid ? "flex-row-reverse" : "flex-row";
  const messageBodyClass =
    uid === props.auth.currentUser.uid
      ? "sent-message-bg text-right"
      : "received-message-bg";
  const imageClass = uid === props.auth.currentUser.uid ? "ml-2" : "mr-2";

  return (
    <div className={`px-3 py-2 flex no-wrap items-start ${messageClass}`}>
      <div>
        {/* <img
          className={`block rounded-full object-cover w-10 ${imageClass}`}
          src={photoURL || "https://i.imgur.com/rFbS5ms.png"}
          alt="{user}'s pfp"
        /> */}
      </div>
      <div
        className={`block w-80 break-words p-2 rounded-md ${messageBodyClass}`}
      >
        <p className="text-xs">{user}</p>
        <p>{body}</p>
      </div>
    </div>
  );
}
